@import url(https://fonts.googleapis.com/css2?family=Jura:wght@300;400;500;515;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jura:wght@300;400;500;515;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f5f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo{font-family:"Jura",sans-serif;display:flex;flex-flow:column nowrap;align-items:center;margin:20px 0;height:150px}.logo h2{font-size:95px;text-shadow:.1rem .1rem #ff1493,-0.1rem -0.1rem #40e0d0;margin:0;color:#000}.logo h1{margin:0;position:relative}.logo h1 .highlight{font-size:32px;display:inline-block;line-height:0em;padding-bottom:.4em;color:#000;background-color:#ff0}
.login-logo{font-family:"Jura",sans-serif;display:flex;flex-flow:column nowrap;align-items:center;justify-content:space-between;margin:20px 0;height:40%}.login-logo .login-color-text{font-size:2000%;text-shadow:.3rem .3rem #ff1493,-0.3rem -0.3rem #40e0d0;margin:0;color:#000}.login-logo .login-name{margin:0;position:relative}.login-logo .login-name .login-highlight{font-size:325%;display:inline-block;line-height:0em;padding-bottom:.4em;color:#000;background-color:#ff0}
